import { Component, OnInit } from '@angular/core';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs/Observable';
import {startWith} from 'rxjs/operators/startWith';
import {map} from 'rxjs/operators/map';
import {UtilsService} from '../../services/utils.service';
import {UserSettingsService} from '../../services/user-settings.service';
import {Ndo6Service, IMarker, IMapElement} from '../../services/ndo6.service';
import {Ndo6IconsService} from '../../services/ndo6-icons.service';
import {MapsService} from '../../services/maps.service';
import {InteractionService} from '../../services/interaction.service';
import {LogService} from '../../services/log.service';
import _ from 'lodash';




@Component({
  selector: 'app-overpage-settings',
  templateUrl: './overpage-settings.html'
})
export class OverpageSettingsComponent implements OnInit {

  constructor(public user: UserSettingsService) { }

  ngOnInit() {
  }
}


@Component({
  selector: 'app-overpage-test',
  templateUrl: './overpage-test.html'
})
export class OverpageTestComponent implements OnInit {

  constructor(public user: UserSettingsService,
              private interaction: InteractionService,
              private u: UtilsService,
              public log: LogService) { }
  positions = [];
  lines = [];
  types = {
    error: true,
    info: false,
    warning: false,
    special: false
  };
  updateFilter() {
    const self = this;
    const types = [];
    _.keys(self.types).forEach(t => {
      if (self.types[t]) { types.push(t); }
    });
    self.lines = _.filter(self.log.lines, l => types.indexOf(l.type) > -1);
  }
  clearLog() {
    this.log.clear();
  }
  ngOnInit() {
    this.updateFilter();
  }
  updateSettings() {
    this.user.update();
  }
  sendPos() {
    const self = this;
    self.interaction.position({
      latitude: 11.4363463,
      longitude: 43.52164346,
      timestamp: Date.now()
    }, (err) => {
      if (err)  {
        self.log.error(err);
      } else {
        self.log.info('Position sending successfully');
      }
    });
  }
  testStorage() {
    const self = this;
    const test_key = 'NDO6_TEST_STORAGE_KEY';
    self.u.storage.set(test_key, {name: 'test-object', value: 123.63437});
    const o = self.u.storage.get(test_key);
    self.log.info('STORAGGE TEST: ', o || 'undefined');
  }
  deleteMap() {
    this.interaction.deleteMap({view: this.user.settings.map});
  }
  emptyMap() {
    this.interaction.emptyMap({view: this.user.settings.map});
  }
  resetDb() {
    this.interaction.resetDb();
  }
  test() {
    const self = this;
    self.interaction.test({view: this.user.settings.map}, (err, result) => {
      if (err)  {
        console.error(err);
      } else {
        self.log.info('Server test successfully', result);
      }
    });
  }
}


@Component({
  selector: 'app-overpage-marker',
  templateUrl: './overpage-marker.html'
})
export class OverpageMarkerComponent implements OnInit {

  constructor(public u: UtilsService,
              private ndo6: Ndo6Service) { }
  followed = false;
  marker: any = null;
  ngOnInit() {
    this.followed = this.ndo6.followMarker === this.u.overpage.options.marker;
    this.marker = (this.u.overpage.options.marker || {}).ndo6 || {};
  }
  changeOption() {
    if (this.followed) {
      this.ndo6.followMarker = this.u.overpage.options.marker;
      this.ndo6.checkPos();
    } else {
      this.ndo6.followMarker = null;
    }
  }
}


@Component({
  selector: 'app-overpage-map',
  templateUrl: './overpage-map.html'
})
export class OverpageMapComponent implements OnInit {
  mapName: FormControl = new FormControl();
  constructor(private u: UtilsService,
              private log: LogService,
              private user: UserSettingsService,
              private interaction: InteractionService,
              private ndo6: Ndo6Service,
              private maps: MapsService) { }
  data: any = {
    name: 'New Map',
    nick: 'my nickname'
  };
  options = [];
  filteredOptions: Observable<string[]>;
  logged = false;
  error = '';
  elements = [];
  owners = [];
  ngOnInit() {
    const self = this;
    self.logged = !!self.user.settings.token;
    self.data.name = self.user.settings.map || self.data.name;
    self.data.nick = self.user.settings.nick || self.data.nick;
    self.interaction.getMaps((err, maps) => self.options = _.map(maps || [], (m) => m.name));
    self.filteredOptions = self.mapName.valueChanges
      .pipe(
        startWith(''),
        map(val => self.filter(val))
      );
    self.maps.elements.forEach(e => {
      switch (e.ndo6.type) {
        case self.ndo6.markers.owner:
          if (e.ndo6.owner !== self.user.settings.nick) {
            self.owners.push({
              name: e.ndo6.owner,
              marker: e
            });
          }
          break;
        case self.ndo6.markers.point:
          self.elements.push({
            name: e.ndo6.name,
            marker: e
          });
          break;
      }
    });
  }
  filter(val: string): string[] {
    return this.options.filter(option =>
      option.toLowerCase().indexOf(val.toLowerCase()) === 0);
  }
  resetError() {
    this.error = '';
  }
  validate() {
    return this.u.validate(this.data.name) &&
      this.u.validate(this.data.password, '^[a-zA-Z0-9]{4,10}$') &&
      this.u.validate(this.data.nick);
  }
  logout() {
    this.u.idle = true;
    this.interaction.logout({}, (err) => {
      this.ndo6.logout(true);
    });
  }
  handleErr(err) {
    if (err) {
      this.error = this.u.getErrorMessage(err);
    }
    this.u.idle = false;
    return !!this.error;
  }
  login() {
    this.u.idle = true;
    const info = {
      name: this.data.name,
      owner: this.data.nick,
      password: this.data.password,
      position: this.ndo6.getLastPos()
    };
    this.error = '';
    this.interaction.login(info, (err, result) => {
      if (this.handleErr(err)) { return; }
      delete this.data.password;
      if (result.system) {
        this.user.system.token = result.token;
        this.u.closeOverpage();
      } else {
        this.ndo6.login(result.token, this.data);
      }
    });
  }
  create() {
    this.error = '';
    const info = {
      name: this.data.name,
      owner: this.data.nick,
      password: this.data.password,
      center: ''
    };
    this.interaction.create(info, (err, result) => {
      if (this.handleErr(err)) { return; }
      delete this.data.password;
      this.log.info('CREATE', result);
      this.user.logdata(result.token, this.data);
      this.u.closeOverpage();
      this.ndo6.checkState();
    });
  }
  addElement() {
    this.u.openOverpage('elements');
  }
  find(m: IMarker) {
    this.ndo6.centerMap(m);
    this.u.closeOverpage();
  }
}


@Component({
  selector: 'app-overpage-elements',
  templateUrl: './overpage-elements.html'
})
export class OverpageElementsComponent implements OnInit {
  constructor(public u: UtilsService,
              private ndo6: Ndo6Service) {
    // this.u.overpage.back = { type: 'elements' };
  }
  elements = [{
    title: 'Point',
    icon: 'place',
    desc: 'Add new place marker in current position',
    type: this.ndo6.mapElements.point
  }, {
    title: 'Way',
    icon: 'timeline',
    desc: 'Define a new way from a place to an other',
    type: this.ndo6.mapElements.way
  }];
  ngOnInit() {
    this.u.overpage.options = this.u.overpage.options || {};
    this.u.overpage.options.elementType = '';
    this.u.overpage.options.element = {};
    this.u.overpageBack({ type: 'map' });
  }
  select(type) {
    this.u.overpage.options.elementType = type;
  }
  addToMap() {
    this.ndo6.addToMap(this.u.overpage.options.element);
    this.u.closeOverpage();
  }
}


@Component({
  selector: 'app-element-point',
  templateUrl: './elements/element-point.html'
})
export class MapElementPointComponent implements OnInit {
  constructor(private u: UtilsService,
              private ndo6: Ndo6Service,
              public graph: Ndo6IconsService) {
  }
  private info: IMapElement = {
    position: {},
    type: '',
    name: 'New Point',
    content: {}
  };
  ngOnInit() {
    const e: IMapElement = _.clone(this.info);
    e.type = this.ndo6.mapElements.point;
    e.content.desc = '';
    e.content.icon = this.graph.getDef('flag');
    e.content.color = this.graph.defaults.color;
    e.content.position = this.ndo6.getCurrentPos();
    this.u.overpage.options.element = e;
  }
  setIcon(idef) {
    const source = this.u.overpage.options.element.content;
    const color = source.icon.color || this.graph.defaults.color;
    source.icon = this.graph.getDef(idef.code);
    if (source.icon.colors.indexOf(color) < 0) {
      source.icon.color = this.graph.defaults.color;
    } else {
      source.icon.color = color;
    }
  }
}


@Component({
  selector: 'app-element-way',
  templateUrl: './elements/element-way.html'
})
export class MapElementWayComponent implements OnInit {
  constructor(private u: UtilsService,
              private ndo6: Ndo6Service) {
  }
  private info: IMapElement = {
    position: {},
    type: '',
    name: 'New Way',
    content: {}
  };
  ngOnInit() {
    const e: IMapElement = _.clone(this.info);
    e.type = this.ndo6.mapElements.way;
    e.content.desc = '';
    e.content.start = '';
    e.content.end = '';
    this.u.overpage.options.element = e;
  }
}




